html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "DotGothic16", sans-serif;
  font-size: 1.6rem;
  background-color: #d05030;
}

p {
  font-size: 2.4rem;
  color: #000000;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 60%;
  }
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.2rem;
}

.hidden {
  display: none !important;
}

.text-align-left {
  text-align: left;
}

.btn {
  border-style: none;
  cursor: pointer;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-size: 2.8rem;
}

.btn-block {
  display: block;
  border-style: none;
  cursor: pointer;
  padding: 0.5rem 2rem;
  border-radius: 0.5rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  text-align: center;
}

.btn-primary {
  background-color: #d05030;
  color: #f8f8f8;
  transition: background-color 0.3s ease;
}
.btn-primary:hover {
  background-color: #f8a050;
}

.btn-secondary {
  background-color: #f8a050;
  color: #f8f8f8;
  text-align: center;
  transition: background-color 0.3s ease;
}
.btn-secondary:hover {
  background-color: #676767;
}

.btn-filter {
  background-color: #000000;
  color: #f8f8f8;
  padding: 0.3rem 0.8rem;
  border: 0.3rem solid #f8f8f8;
  cursor: pointer;
  text-align: left;
  margin-bottom: 1rem;
}

.selected {
  border: 0.4rem solid #f8f8f8;
  box-sizing: border-box !important;
}

header {
  background-color: #f8a050;
}
header h1 {
  font-family: "DotGothic16", sans-serif;
  font-size: 5rem;
  padding: 2rem 0;
  color: #f8f8f8;
  text-align: center;
}

.pokedex {
  width: 100%;
}
.pokedex .pokedex-list {
  overflow-y: scroll; /* A침ade la barra de desplazamiento vertical */
  border: 0.4rem solid #f8f8f8;
  background-color: #000000;
  height: 55rem;
}
.pokedex .pokedex-list ul {
  list-style: none;
  padding: 0;
}
.pokedex .pokedex-list ul li {
  width: 100%;
  padding: 2rem 1rem;
  border-radius: 0.5rem;
  font-size: 2.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.3s ease;
  border-bottom: 1px solid #676767;
}
.pokedex .pokedex-list ul li:hover {
  background-color: #676767;
}
.pokedex .pokedex-list ul li p {
  color: #f8f8f8;
  font-size: 2.4rem;
  margin: 0 1rem;
}
.pokedex .poke-img {
  border: 0.4rem solid #f8f8f8;
  background-color: #f8f8f8;
  position: relative;
  display: inline-block;
}
.pokedex .poke-img img {
  border: 0.4rem solid #000000;
  width: 8rem;
  filter: grayscale(100%);
}
.pokedex .poke-img::after {
  content: ""; /* Se utiliza un pseudo-elemento para crear el overlay */
  position: absolute;
  top: 0.4rem;
  left: 0.4rem;
  width: calc(100% - 0.8rem);
  height: calc(100% - 0.8rem);
  background-color: rgba(65, 134, 0, 0.5); /* Color verde con opacidad del 50% */
  pointer-events: none; /* Para permitir la interacci칩n con el contenido del contenedor */
}

.pokedex-list::-webkit-scrollbar {
  width: 12px; /* Ancho de la barra de desplazamiento */
}

.pokedex-list::-webkit-scrollbar-thumb {
  background-color: #d05030; /* Color de la parte deslizante de la barra */
}

.pokedex-list::-webkit-scrollbar-track {
  background-color: #f8a050; /* Color del fondo de la barra de desplazamiento */
}

.poke-info {
  display: flex;
  justify-content: space-between;
}
.poke-info span {
  font-size: 2.8rem;
}

.footer {
  padding: 2rem 0;
  text-align: center;
}
.footer p {
  font-size: 3rem;
}

.poke-page {
  display: block;
  margin-bottom: 2rem;
}
@media (min-width: 480px) {
  .poke-page {
    display: grid;
    grid-template-columns: 30% 70%;
    gap: 2rem;
    width: calc(100% - 2rem);
  }
}
.poke-page p {
  color: #f8f8f8;
  font-size: 2.4rem;
}
.poke-page .pokedex-img {
  margin-bottom: 2rem;
}
@media (min-width: 480px) {
  .poke-page .pokedex-img {
    margin-bottom: unset;
  }
}
.poke-page .pokedex-img img {
  width: 100%;
  border: 0.4rem solid #000000;
}
.poke-page .pokedex-description {
  grid-column: 1/3;
}
.poke-page .pokedex-description p {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.poke-page .pokedex-description .poke-type {
  color: #f8f8f8;
  margin-top: 2rem;
}
@media (min-width: 480px) {
  .poke-page .pokedex-description .poke-type {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
.poke-page .pokedex-description .poke-type p {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .poke-page .pokedex-description .poke-type p {
    margin: 0;
  }
}
.poke-page .pokedex-description .poke-type h3 {
  font-size: 2.8rem;
  padding: 1rem 3rem;
  border-radius: 3rem;
  margin-bottom: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .poke-page .pokedex-description .poke-type h3 {
    margin: 0;
  }
}
.poke-page .pokedex-description .poke-type .type-1 {
  text-transform: uppercase;
}
.poke-page .pokedex-info {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 480px) {
  .poke-page .pokedex-info {
    margin-bottom: unset;
  }
}
.poke-page .pokedex-info .pokedex-name p {
  margin: 1rem;
}
.poke-page .pokedex-info .pokedex-name span {
  font-size: 4rem;
}
@media (min-width: 992px) {
  .poke-page .pokedex-info .pokedex-name span {
    font-size: 6rem;
  }
}
.poke-page .pokedex-info .pokedex-extra p {
  font-size: 2.4rem;
}

.pokedex-description,
.pokedex-info {
  background-color: #000000;
  padding: 2rem;
  border: 0.4rem solid #f8f8f8;
}

.Grass {
  background-color: #4aa55a;
}

.Bug {
  background-color: #95ac59;
}

.Poison {
  background-color: #ad7bbd;
}

.Water {
  background-color: #568ed3;
}

.Rock {
  background-color: #928d94;
}

.Psychic {
  background-color: #ea73ab;
}

.Normal {
  background-color: #c89674;
}

.Ice {
  background-color: #8aa2e8;
}

.Ground {
  background-color: #ad734a;
}

.Ghost {
  background-color: #584166;
}

.Flying {
  background-color: #8787bd;
}

.Fire {
  background-color: #e26b3a;
}

.Fighting {
  background-color: #a96976;
}

.Electric {
  background-color: #dab635;
}

.Dragon {
  background-color: #e3885d;
}

.pokedex-options {
  padding: 1rem 0;
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .pokedex-options {
    display: flex;
    justify-content: space-around;
  }
}
.pokedex-options select {
  background-color: #000000;
  color: #f8f8f8;
  border: 0.3rem solid #f8f8f8;
  padding: 0 1rem;
  margin-bottom: 1rem;
}
.pokedex-options .filter-options,
.pokedex-options .sort-options {
  display: grid;
  gap: 1rem;
}
@media (min-width: 768px) {
  .pokedex-options .filter-options,
  .pokedex-options .sort-options {
    display: unset;
  }
}

.modal {
  background-color: #000000;
  color: #f8f8f8;
  border: 0.4rem solid #f8f8f8;
  text-align: center;
}
.modal::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}
.modal h2 {
  margin-bottom: 5rem;
  margin-top: 5rem;
}
.modal .types-grid h3 {
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .modal .types-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .modal .types-grid h3 {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .modal .types-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
  .modal .types-grid h3 {
    margin-bottom: 0;
  }
}
@media (min-width: 992px) {
  .modal .types-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
  }
  .modal .types-grid h3 {
    margin-bottom: 0;
  }
}
.modal .modal-button {
  margin-top: 5rem;
}
.modal .modal-button button {
  margin-bottom: 1rem;
}
@media (min-width: 480px) {
  .modal .modal-button button {
    margin-bottom: unset;
  }
}
@media (min-width: 480px) {
  .modal .modal-button {
    text-align: unset;
    display: flex;
    justify-content: space-between;
  }
}